@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap);
* {
	font-family: 'Cairo', sans-serif;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.app {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.card {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 20px;
}

.current-weather {
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: 20vh;
	background: #bbe7ca;
	padding: 20px;
	border-radius: 10px;
}

.main {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 1.5rem;
}

.main-image {
	display: flex;
	flex-direction: row;
}

.main-image img {
	height: 7rem;
}

.main-text {
	line-height: 7rem;
}

.description {
	display: flex;
	flex-direction: column;
	margin: 5px;
}

.sevendays-weather {
	margin: auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	width: 100%;
}

@media screen and (max-width: 883px) {
	h1,
	h3 {
		font-size: medium;
	}
}

@media screen and (max-width: 480px) {
	h1,
	h3 {
		font-size: medium;
	}
	.section {
		display: flex;
		flex-direction: column;
	}

	.current-weather {
		width: 90vw;
		height: 300px;
		display: inline-block;
		flex-direction: column;
	}

	.main-image img {
		height: 5rem;
	}

	.main-text {
		font-size: medium;
		line-height: 5rem;
	}

	.description {
		font-size: small;
	}
}

button {
	border-radius: 5px;
	background: #3da95f;
	white-space: nowrap;
	padding: 10px 15px;
	color: white;
	font-size: 1rem;
	outline: none;
	border: none;
	cursor: pointer;
	width: 100px;
}

input {
	display: block;
	padding: 10px;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background: none;
	outline: none;
	border: 0;
	border-radius: 5px;
	font-size: 1rem;
	background-color: #e7f4e8;
	margin: 5px;
}

div.hidden {
	display: none;
}

.section {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
}

.search-form {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	width: 90%;
}

.option__container {
	display: flex;
	justify-content: space-around;
}

.option__container--btn {
	margin: 5px;
	text-transform: uppercase;
}

.loading {
	width: 3em;
	height: 3em;
	margin: 15px;
	border-radius: 50%;
	border: 3px solid #e7f4e8;
	border-top: 3px solid #3da95f;
	animation: spin 2s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

@media screen and (max-width: 62rem) {
	.search-form {
		display: flex;
		flex-direction: column;
		margin: 2rem;
	}
}

